import { PageHeader } from "../../../../shared/PageHeader";
import { PageFooter } from "../../../../shared/PageFooter";
import "./style.css";
import { NewVacationRequestForm } from "../../../../features/form";
import { useRef } from "react";
// import { UseOfVacationsTable } from "../../../../features/table";
import { Toast } from 'primereact/toast';


export const CreateRequest = ({ i18n,currentUser, employees, departments, vacationTypes }) => {
  const { t } = i18n;

  const toastRef = useRef(null);

  return (
    <>
      <Toast ref={toastRef} />

      <main className="page CreateRequest_page container_local">
        <PageHeader title={t("menu.subMenuVacations.menuItemCreateRequest")} />

        <div className="page_content CreateRequest_page_content">
          <NewVacationRequestForm
            i18n={i18n}
            currentUser={currentUser}
            employees={employees}
            departments={departments}
            vacationTypes={vacationTypes}
            toastRef={toastRef}
          />
          {/* <UseOfVacationsTable i18n={i18n}/> */}
        </div>

        <PageFooter />
      </main>
    </>
  );
};
