import { useEffect, useState } from "react";
import { CreateRequest as UI } from "./ui/CreateRequest";
import { useTranslation } from "react-i18next";
import { api } from "../../../shared/api";
import { Loading } from "../../../shared/Loading";
import { Error } from "../../../shared/Error";
import { itemsPerPageForAllItems } from "../../../entities/itemsPerPageForAllItems";

const CreateRequest = ({currentUser}) => {
  const [componentStatus, setComponentStatus] = useState({ status: "loading" });

  const { i18n } = useTranslation();

  useEffect(() => {
    (async () => {
        
      const employeesResponse = await api.employees.get({itemsPerPage:itemsPerPageForAllItems});

      if (employeesResponse.error || employeesResponse.warning) {
        setComponentStatus({
          status: "error",
          data: employeesResponse.error || employeesResponse.warning,
        });
        return;
      }

      const departmentsResponse = await api.departments.get({itemsPerPage:itemsPerPageForAllItems})

      if (departmentsResponse.error || departmentsResponse.warning) {
        setComponentStatus({
          status: "error",
          data: departmentsResponse.error || departmentsResponse.warning,
        });
        return;
      }

      const vacationTypesResponse = await api.vacationTypes.get({itemsPerPage:itemsPerPageForAllItems})

      if (vacationTypesResponse.error || vacationTypesResponse.warning) {
        setComponentStatus({
          status: "error",
          data: vacationTypesResponse.error || vacationTypesResponse.warning,
        });
        return;
      }

      setComponentStatus({status:"success",data:{employeesResponse,departmentsResponse,vacationTypesResponse}})

    })();
  }, []);

  if (componentStatus.status === "loading") return <Loading />;
  if (componentStatus.status === "error") return <Error error={componentStatus.data}/>;
  if(componentStatus.status === "success") return <UI i18n={i18n} employees={componentStatus.data.employeesResponse["hydra:member"]} departments={componentStatus.data.departmentsResponse["hydra:member"]} vacationTypes={componentStatus.data.vacationTypesResponse["hydra:member"]} currentUser={currentUser}/>;
  
};

export default CreateRequest;
